<template>
  <div class="take-part-list">
    <vote-statistics :pageName="'vote'" :data="data"/>
  </div>
</template>

<script>
import VoteStatistics from '@/components/VoteSurveyParticipation';
export default {
  props: {
    pageName: String
  },
  data() {
    return {
      data: {}
    };
  },
  components: {
    VoteStatistics
  }
};
</script>

<style lang="stylus" scoped>
.take-part-list
  position absolute
  top 0
  left 0
  bottom 0
  right 0
  background-color #fff
</style>





