<template>
  <div class="vote-detail">
    <!-- main -->
    <detail-main :detail="detail" />
    <!-- form -->

    <question-list v-show="!isAdmin" class="question" :class="{'active': !showStatis}" :showStatis="showStatis">
        <div class="collaps"><a href="javascript:" :class="{'slide-up': showStatis, 'slide-down': !showStatis}"
          @click="showStatis = !showStatis"></a></div>
    </question-list>

    <!-- statistics -->
    <div class="statis">
      <survey-statistics :showStatis="showStatis"/>
    </div>
  </div>
</template>

<script>
import service from '@/service';
import DetailMain from '@/components/VoteSurveyDetail';
import QuestionList from './SurveyQuestionList';
import SurveyStatistics from '@/components/VoteSurveyParticipation';
export default {
  data() {
    return {
      isAdmin: false,
      showStatis: true,
      detail: {},
      lists: {
        hadTakePartIn: [],
        notTakePartIn: []
      },
      linkTo: this.$route.fullPath + '/statistics'
    };
  },
  created() {
    service.vote
      .getDetail({ id: this.$route.params.id })
      .then(res => {
        if (res.data.code === 200) {
          this.detail = res.data.data.detail;
        }
      })
      .catch(err => {
        console.log(err);
      });
  },
  methods: {},
  components: {
    DetailMain,
    QuestionList,
    SurveyStatistics
  }
};
</script>

<style lang="stylus" scoped>
@import "../../common/stylus/base"

.vote-detail
  position absolute
  top 0
  bottom 0
  right 0
  left 0
  display flex
  flex-direction column
  background-color #f4f4f4
  font-size 0
  .question
    margin-bottom 0.2rem
    &.active
      margin-bottom 1.4rem
    .collaps
      padding 0.3rem 0 0.5rem
      background-color #fff
      text-align center
      font-size 0.4rem
      color #777
      .slide-up::after
        content "查看详情"
        padding-right 0.4rem
        background url("../../assets/arrow-down.png") no-repeat right 0.14rem
        background-size 0.3rem 0.192rem
      .slide-down::after
        content "收起"
        padding-right 0.4rem
        background url("../../assets/arrow-up.png") no-repeat right 0.1rem
        background-size 0.3rem 0.192rem
  .statis
    flex-grow 1
    flex-shrink 0
    position relative

</style>




