<template>
  <div class="survey-detail">
    <detail-main :detail="detail"/>
    <question-list />
  </div>
</template>

<script>
import service from '@/service';
import DetailMain from '@/components/VoteSurveyDetail';
import QuestionList from './SurveyQuestionList';
export default {
  data() {
    return {
      detail: {}
    };
  },
  created() {
    service.survey
      .getDetail({ id: this.$route.params.id })
      .then(res => {
        if (res.data.code === 200) {
          this.detail = res.data.data.detail;
        }
      })
      .catch(err => {
        console.log(err);
      });
  },
  methods: {
    submitForm() {

    }
  },
  components: {
    DetailMain,
    QuestionList
  }
};
</script>

<style lang="stylus" scoped>
@import "../../common/stylus/base"
</style>



