<template>
  <div class="vote-detail">
    <!-- main -->
    <detail-main :detail="detail" />
    <!-- statistics -->
    <router-link class="statistics" :to="linkTo">
      <span class="icon"></span>
      <span class="not">未参与{{'21'}}</span>
      <span class="had">已参与{{'24'}}</span>
      <span class="arrow">&gt;</span>
    </router-link>
    <!-- form -->
    <div class="progress">
      <div class="top">
        <span class="icon radio"></span>
        <span class="type">{{'单选'}}</span>
      </div>
      <ul class="lists">
        <li class="item">
          <p class="title">选项标题</p>
          <div class="bar">
            <span class="sum">得票：{{'30'}}</span>
            <progress class="pro" value="22" max="100"></progress>
            <span class="per">{{'45%'}}</span>
          </div>
        </li>
        <li class="item">
          <p class="title">选项标题</p>
          <div class="bar">
            <span class="sum">得票：{{'30'}}</span>
            <progress class="pro" value="22" max="100"></progress>
            <span class="per">{{'45%'}}</span>
          </div>
        </li>
        <li class="item">
          <p class="title">选项标题</p>
          <div class="bar">
            <span class="sum">得票：{{'30'}}</span>
            <progress class="pro" value="22" max="100"></progress>
            <span class="per">{{'45%'}}</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import service from '@/service';
import DetailMain from '@/components/VoteSurveyDetail';
export default {
  data() {
    return {
      detail: {},
      lists: {
        hadTakePartIn: [],
        notTakePartIn: []
      },
      linkTo: this.$route.fullPath + '/statistics'
    };
  },
  created() {
    service.vote
      .getDetail({ id: this.$route.params.id })
      .then(res => {
        if (res.data.code === 200) {
          this.detail = res.data.data.detail;
        }
      })
      .catch(err => {
        console.log(err);
      });
  },
  methods: {},
  components: {
    DetailMain
  }
};
</script>

<style lang="stylus" scoped>
@import "../../common/stylus/base"

.vote-detail
  background-color #f4f4f4
  font-size 0
  .statistics
    display flex
    align-items center
    height 0.88rem
    padding 0 0.2rem
    margin-bottom 0.2rem
    background-color #fff
    font-size 0.34rem
    .icon
      width 0.4rem
      height 0.4rem
      margin-right 0.2rem
      background url("../../assets/statistic.png") no-repeat
      background-size cover
    .not
      margin-right 0.5rem
    .arrow
      flex-grow 1
      text-align right
      color #aaa
  .progress
    padding 0 0.2rem
    background-color #fff
    .top
      display flex
      align-items center
      height 0.6rem
      .icon
        width 0.4rem
        height 0.4rem
      .icon.radio
        background url("../../assets/radio-check-bg.png") no-repeat
        background-size cover
      .icon.multi
        background url("../../assets/multi-check-bg.png") no-repeat
        background-size cover
      .type
        margin-left 0.2rem
        font-size 0.36rem
    .lists
      .title
        margin 0.1rem 0
        font-size 0.38rem
      .bar
        display flex
        align-items center
        padding-bottom 0.15rem
        border-bottom 0.01rem solid #eee
        font-size 0.34rem
        color #777
        .pro
          flex-grow 1
          margin 0 0.15rem
          border 0.01rem solid $color-main
          border-radius 0.3rem
          &::-webkit-progress-inner-element
            border-radius 0.3rem
          &::-webkit-progress-value
            background-color $color-main
            border-radius 0.3rem
          &::-webkit-progress-bar
            background-color #fff
            border-radius 0.3rem

</style>




