<template>
  <form class="question-form" @submit.prevent="submitForm">
    <ul class="question-list" v-show="!showStatis">
      <li class="item" data-type="radio">
        <p class="title">{{'1、'}}标题标题标题标</p>
        <ul class="options">
            <li class="option">
              <input type="radio" name="x" id="radio_1">
              <label for="radio_1"><span class="icon"></span><span>D、</span>选项一</label>
            </li>
             <li class="option">
              <input type="radio" name="x" id="radio_2">
              <label for="radio_2"><span class="icon"></span><span>D、</span>选项一</label>
            </li>
             <li class="option">
              <input type="radio" name="x" id="radio_3">
              <label for="radio_3"><span class="icon"></span><span>D、</span>选项一</label>
            </li>
        </ul>
      </li>
      <li class="item" data-type="radio">
        <p class="title">{{'1、'}}标题标题标题标</p>
        <ul class="options">
            <li class="option">
              <input type="radio" name="x" id="radio_1">
              <label for="radio_1"><span class="icon"></span><span>D、</span>选项一</label>
            </li>
             <li class="option">
              <input type="radio" name="x" id="radio_2">
              <label for="radio_2"><span class="icon"></span><span>D、</span>选项一</label>
            </li>
             <li class="option">
              <input type="radio" name="x" id="radio_3">
              <label for="radio_3"><span class="icon"></span><span>D、</span>选项一</label>
            </li>
        </ul>
      </li>
      <li class="item" data-type="multi">
        <p class="title">{{'1、'}}标题标题标题标</p>
        <ul class="options">
          <li class="option">
            <input type="checkbox" id="multi_1">
            <label for="multi_1"><span class="icon"></span><span>D、</span>选项一</label>
          </li>
          <li class="option">
            <input type="checkbox" id="multi_2">
            <label for="multi_2"><span class="icon"></span><span>D、</span>选项一</label>
          </li>
          <li class="option">
            <input type="checkbox" id="multi_3">
            <label for="multi_3"><span class="icon"></span><span>D、</span>选项一</label>
          </li>
        </ul>
      </li>
      <li class="item" data-type="short">
        <p class="title">{{'1、'}}标题标题标题标</p>
        <div class="txt">
          <textarea name="" id="" cols="30" rows="5"></textarea>
        </div>
      </li>
    </ul>
    <slot></slot>
    <div class="btn" v-if="!isAdmin">
      <button type="submit">提交</button>
    </div>
  </form>
</template>

<script>
export default {
  name: 'SurveyDetailAdmin',
  props: {
    showStatis: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isAdmin: true
    };
  }
};
</script>

<style lang="stylus" scoped>
@import "../../common/stylus/base";
.question-form
  font-size 0
  .question-list
    .item
      margin-top 0.2rem
      padding 0.2rem 0
      box-shadow 0 2px 8px 1px #ddd
      background-color #fff
      font-size 0.38rem
      &[data-type="radio"]>.title::after
        content "【单选】"
        color $color-main
      &[data-type="multi"]>.title::after
        content "【多选】"
        color $color-main
      &[data-type="short"]>.title::after
        content "【简答】"
        color $color-main
      .title
        padding-left 0.2rem
        font-size 0.36rem
      .txt
        display flex
        padding 0.2rem 0.5rem
        textarea
          flex-grow 1
          padding 0.05rem
          border 0.01rem solid #ccc
          resize none
          text-align justify
      .options
        .option
          input
            display none
          label
            display flex
            align-items center
            padding-left 0.3rem
            height 0.8rem
            font-size 0.36rem
          input[type="radio"]+label>.icon
            width 0.4rem
            height 0.4rem
            padding 0.05rem
            margin 0 0.25rem 0 0.3rem
            border 0.02rem solid #ccc
            border-radius 50%
          input[type="radio"]:checked+label
            background-color #f8ecec
            .icon
              background-color $color-main
              background-clip content-box
          input[type="checkbox"]+label>.icon
            width 0.36rem
            height 0.36rem
            margin 0 0.25rem 0 0.3rem
            background-color $color-main
          input[type="checkbox"]:checked+label
            background-color #f8ecec
            .icon
              background $color-main url("../../assets/checked-bg.png") no-repeat center
              background-size contain
  .btn
    margin 0.5rem 0 0.8rem
    text-align center
    button
      width 4rem
      height 0.8rem
      border none
      border-radius 0.05rem
      background-color $color-main
      color #fff
      font-size 0.36rem
</style>




