<template>
  <div class="vote-detail">
    <detail-main :detail="detail"/>
    <div class="form">
      <div class="type multi">单选</div>
      <form class="question-form" @submit.prevent="submitForm">
        <ul check-type="radio">
          <li><input type="radio" name="a" id="radio_1">
            <label for="radio_1"><span class="icon"></span><span>A、</span>选项一</label>
          </li>
          <li><input type="radio" name="a" id="radio_2">
            <label for="radio_2"><span class="icon"></span><span>B、</span>选项一</label>
          </li>
          <li><input type="radio" name="a" id="radio_3">
            <label for="radio_3"><span class="icon"></span><span>C、</span>选项一</label>
          </li>
          <li><input type="radio" name="a" id="radio_4">
            <label for="radio_4"><span class="icon"></span><span>D、</span>选项一</label>
          </li>
        </ul>
        <ul check-type="multi">
          <li><input type="checkbox" id="multi_1">
            <label for="multi_1"><span class="icon"></span><span>D、</span>选项一</label>
          </li>
          <li><input type="checkbox" id="multi_2">
            <label for="multi_2"><span class="icon"></span><span>D、</span>选项一</label>
          </li>
          <li><input type="checkbox" id="multi_3">
            <label for="multi_3"><span class="icon"></span><span>D、</span>选项一</label>
          </li>
          <li><input type="checkbox" id="multi_4">
            <label for="multi_4"><span class="icon"></span><span>D、</span>选项一</label>
          </li>
        </ul>
        <div class="btn">
          <button type="submit">提交</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import service from '@/service';
import DetailMain from '@/components/VoteSurveyDetail';
export default {
  data() {
    return {
      detail: {}
    };
  },
  created() {
    service.vote
      .getDetail({ id: this.$route.params.id })
      .then(res => {
        if (res.data.code === 200) {
          this.detail = res.data.data.detail;
        }
      })
      .catch(err => {
        console.log(err);
      });
  },
  methods: {
    submitForm() {}
  },
  components: {
    DetailMain
  }
};
</script>

<style lang="stylus" scoped>
@import "../../common/stylus/base"

.vote-detail
  padding-bottom 0.8rem
  background-color #f4f4f4
  font-size 0
  .form
    position relative
    padding-top 0.2rem
    background-color #fff
    margin-bottom 2rem
    .type
      height 0.5rem
      padding-left 1rem
      margin-bottom 0.3rem
      line-height 0.5rem
      font-size 0.4rem
    .type.multi
      background url("../../assets/multi-check-bg.png") no-repeat 0.2rem 0
      background-size contain
    .type.radio
      background url("../../assets/radio-check-bg.png") no-repeat 0.2rem 0
    input
      display none
    label
      display flex
      align-items center
      height 0.8rem
      font-size 0.36rem
    input[type="radio"]+label>.icon
      width 0.4rem
      height 0.4rem
      padding 0.05rem
      margin 0 0.25rem 0 0.3rem
      border 0.02rem solid #ccc
      border-radius 50%
    input[type="radio"]:checked+label
      background-color #f8ecec
      .icon
        background-color $color-main
        background-clip content-box
    input[type="checkbox"]+label>.icon
      width 0.36rem
      height 0.36rem
      margin 0 0.25rem 0 0.3rem
      background-color $color-main
    input[type="checkbox"]:checked+label
      background-color #f8ecec
      .icon
        background $color-main url("../../assets/checked-bg.png") no-repeat center
        background-size contain
    .btn
      position absolute
      width 100%
      bottom -1.7rem
      text-align center
      button
        width 4rem
        height 0.8rem
        border none
        border-radius 0.05rem
        background-color $color-main
        color #fff
        font-size 0.36rem
</style>



