<template>
  <div class="detail-main">
    <p class="title">{{detail.title}}</p>
    <div class="stamp">
      <span class="time">{{detail.time}}</span>
      <span class="author">{{detail.author}}</span>
      <span class="status">{{'状态'}}</span>
    </div>
    <p class="tip">截至日期：{{'2017-05-14'}}</p>
    <p class="tip">每天只能投一次</p>
    <div class="detail-content">
      <p class="des">{{detail.description}}</p>
      <p class="tip" v-if="!isAdmin">您已参与</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'VoteSurveyDetail',
  props: {
    detail: Object
  },
  data() {
    return {
      isAdmin: true
    };
  }
};
</script>

<style lang="stylus" scoped>
@import "../common/stylus/base"

.detail-main
  padding 0.2rem
  background-color #fff
  box-shadow 0 2px 8px 1px #ddd
  .title
    margin-bottom 0.1rem
    font-size 0.48rem
    text-align justify
  .stamp
    display flex
    align-items center
    font-size 0.32rem
    color #777
    .author
      margin-left 0.5rem
    .status
      flex-grow 1
      text-align right
  .tip
    margin-top 0.1rem
    font-size 0.34rem
    color #777
  .detail-content
    padding 0.3rem 0
    font-size 0.36rem
    .des
      word-break break-all
      text-align justify
    .tip
      color $color-main
      text-align center
    .btn
      text-align center
      color #777
</style>



